<template>
  <Header @getSearch="search = $event" />
  <Notes :notes="filterNotes" @del="delNote" @edit="editNote" />
  <Modal
    v-show="showModal"
    :title="modalTitle"
    :desc="modalDesc"
    :id="curID"
    @changeTitle="modalTitle = $event"
    @changeDesc="modalDesc = $event"
    @addOrChange="addOrChange"
    @showOrClose="closeModal"
  />

  <a href="#" class="openModal" @click.prevent="showModal = true">
    <img src="@/assets/img/add-note.svg" alt="Add Note">
  </a>
</template>

<script>
import Header from "@/components/Header.vue";
import Modal from "@/components/Modal.vue";
import Notes from "@/components/Notes.vue";
import { v4 as uuidv4 } from "uuid";

export default {
  name: "App",
  components: {
    Header,
    Modal,
    Notes,
  },
  data() {
    return {
      modalTitle: "",
      modalDesc: "",
      label: localStorage.label ? JSON.parse(localStorage.label) : [],
      showModal: false,
      curID: null,
      search: "",
    };
  },
  computed: {
    filterNotes() {
      return this.search
        ? this.label.filter((item) =>
            item.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.label;
    },
  },
  methods: {
    addOrChange(id) {
      let obj = {
        title: this.modalTitle,
        desc: this.modalDesc,
        date: new Date(Date.now()).toLocaleDateString(),
        id: id || uuidv4(),
      };
      if (id) {
        let idx = this.label.findIndex((item) => item.id == id);
        this.label[idx] = obj;
      } else {
        this.label.push(obj);
      }
      this.curID = null;
      this.closeModal("close");
      // console.log(this.label);
      this.save();
    },
    closeModal(val) {
      if (val === "close") {
        setTimeout(() => {
          this.modalTitle = "";
          this.modalDesc = "";
          this.curID = null;
        }, 400);
        this.showModal = !this.showModal;
      }
    },
    save() {
      localStorage.label = JSON.stringify(this.label);
    },
    delNote(id) {
      let idx = this.label.findIndex((item) => item.id === id);
      this.label.splice(idx, 1);
      this.save();
    },
    editNote(id) {
      const obj = this.label.find((item) => item.id == id);
      // console.log(obj);
      this.modalTitle = obj.title;
      this.modalDesc = obj.desc;
      this.curID = obj.id;
      this.showModal = true;
    },
  },
};
</script>

<style lang="scss">
.openModal {
  border-radius: 16px;
  width: 60px;
  height: 60px;
  background: #fff;
  box-shadow: var(--primaryShadow), var(--secondaryShadow);
  display: grid;
  place-items: center;
  position: fixed;
  right: 3rem;
  bottom: 3rem;
}
</style>