<template>
  <header class="header">
    <h1 class="header__title">Notes</h1>
    <form class="header__form">
      <input type="text" class="header__form-input" placeholder="Search..." v-model="search" />
    </form>
  </header>
</template>

<script>
export default {
  data() {
    return {
      search: "",
    };
  },
  watch: {
    search(val) {
      this.$emit("getSearch", val)
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  padding: 20px 0;
  box-shadow: var(--secondaryShadow);

  &__title {
    margin-bottom: 8px;
    text-align: center;
  }

  &__form {
    text-align: center;

    &-input {
      border: 0;
      border-radius: 6px;
      padding: 10px 16px;
      box-shadow: var(--secondaryShadow), var(--primaryShadow);
    }
  }
}
</style>