<template>
  <div class="notes">
    <div class="container">
      <div class="notes__content">
        <h2 class="notes__title">{{ notes.length ? "All Notes" : "No Notes" }}</h2>
        <button class="notes__checker" @click="grid = !grid" :disabled="notes.length ? false : true">
          <img src="@/assets/img/list.svg" alt="list" class="notes__checker-icon" v-show="grid" />
          <img src="@/assets/img/grid.svg" alt="list" class="notes__checker-icon" v-show="!grid" />
          <b>{{ grid ? "List" : "Grid" }}</b>
        </button>
      </div>
      <div :class="[{ column: !grid }, { notes__grid: true }]">
        <OneNote
          v-for="note in notes"
          :key="note.id"
          :note="note"
          @del="$emit('del', note.id)"
          @edit="$emit('edit', note.id)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import OneNote from "./OneNote.vue";

export default {
  components: {
    OneNote,
  },
  data() {
    return {
      grid: true,
    };
  },
  props: {
    notes: {
      type: Array,
      default: [],
    },
  },
};
</script>

<style lang="scss" scoped>
.notes {
  padding: 50px 0;

  &__content {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__checker {
    border: 0;
    border-radius: 6px;
    padding: 8px 12px;
    background: #fff;
    box-shadow: var(--primaryShadow);
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 14px;
    cursor: pointer;
  }

  &__grid {
    display: grid;
    gap: 20px;

    &.column {
      grid-template-columns: repeat(auto-fit, minmax(255px, 1fr));
    }
  }
}
</style>