<template>
  <div class="notes__item">
    <h3 class="notes__item-title">{{ note.title }}</h3>
    <span class="notes__item-date">{{ note.date }}</span>
    <p class="notes__item-desc">{{ note.desc }}</p>
    <div class="notes__item-control">
      <a href="#!" class="notes__item-edit" @click="$emit('edit', note.id)">
        <img src="@/assets/img/edit.svg" alt="edit" class="notes__item-icon">
        edit
      </a>
      <a href="#!" class="notes__item-delete" @click="$emit('del', note.id)">
        <img src="@/assets/img/delete.svg" alt="delete" class="notes__item-icon">
        delete
      </a>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    note: {
      type: Object,
      default: {
        title: "",
        date: "",
        desc: "",
        id: 0,
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.notes__item {
  border-radius: 16px;
  padding: 20px;
  box-shadow: var(--secondaryShadow), var(--primaryShadow);
  display: grid;
  gap: 6px;
  overflow-wrap: anywhere;

  &-date {
    color: #999;
    font-size: 14px;
  }

  &-control {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 30px;
  }

  &-edit,
  &-delete {
    display: flex;
    align-items: center;
    gap: 8px;
    text-transform: capitalize;
  }

  &-delete {
    color: red;
  }
}
</style>