<template>
  <Transition name="modal">
    <div class="modal" @click="$emit('showOrClose', 'close')">
      <div class="modal__content" @click.stop>
        <h3 class="modal__content-title">
          {{ id ? "Edit note" : "Add note" }}
        </h3>
        <form action="" class="modal__content-form" @submit.prevent="addOrChange">
          <input
            type="text"
            class="modal__content-inp"
            placeholder="Title"
            :value="title"
            @input="$emit('changeTitle', $event.target.value)"
            required
          />
          <textarea
            name=""
            id=""
            cols="30"
            rows="5"
            class="modal__content-area"
            placeholder="Content"
            :value="desc"
            @input="$emit('changeDesc', $event.target.value)"
            required
          ></textarea>
          <div class="modal__content-btns">
            <a
              href="#!"
              class="modal__content-cancel"
              @click="$emit('showOrClose', 'close')"
              >Cancel</a
            >
            <button class="modal__content-add">
              {{ id ? "Edit" : "Add" }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "",
    },
    desc: {
      type: String,
      default: "",
    },
    id: {
      type: String,
      default: null,
    },
  },
  methods: {
    addOrChange() {
      this.$emit("showOrClose", "open");
      this.$emit("addOrChange", this.id);
    },
  },
};
</script>

<style lang="scss" scoped>
.modal {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  display: flex;
  background: rgba(0, 0, 0, 0.4);
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;

  &-enter-active,
  &-leave-active {
    transition: 0.35s;
  }

  &-enter-from,
  &-leave-to {
    opacity: 0;
    scale: 1.2;
  }

  &-enter-to,
  &-leave-from {
    opacity: 1;
    scale: 1;
  }

  &__content {
    border-radius: 16px;
    padding: 30px;
    max-width: 400px;
    width: 100%;
    background: #fff;
    box-shadow: var(--secondaryShadow), var(--primaryShadow);

    &-title {
      margin-bottom: 8px;
      text-align: center;
    }

    &-form {
      display: grid;
      place-items: center;
      gap: 12px;
    }

    &-inp,
    &-area {
      border: 0;
      border-radius: 6px;
      padding: 10px;
      width: 100%;
      box-shadow: var(--primaryShadow);
      font-size: 16px;
    }

    &-area {
      resize: vertical;
    }

    &-btns {
      display: flex;
      gap: 12px;
    }

    &-cancel {
      color: red;
    }

    &-add {
      border: 0;
      color: cornflowerblue;
      background: transparent;
      font-size: 16px;
      cursor: pointer;
    }
  }
}
</style>